import { _decorator, Component, Node } from 'cc';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;

@ccclass('CopyInviteLink')
export class CopyInviteLink extends Component {


    start() {
        this.node.on(Node.EventType.TOUCH_END, this.onClick, this);
    }

    async onClick() {
        const tgWebApp = TelegramWebApp.Instance;
        await tgWebApp.init();

        const user = tgWebApp.getTelegramUser();

        tgWebApp.share("ssilka tut s referalkoi", `i am ${user.username} and my id is ${user.id}`);
    }
}

